.tb_header5_row__ {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: space-between;
  overflow: hidden;
}
.tb_header5_column_content__,
.tb_header5_column_icons__,
.tb_header5_column_logo__ {
  align-items: center;
  display: flex;
  height: 100% !important;
  padding: 0 2%;
  position: relative;
  width: 100%;
}
.tb_header5_column_content__ {
  max-width: 60%;
}
.tb_header5_column_icons__,
.tb_header5_column_logo__ {
  max-width: 30%;
}
.tb_header5_logo__ {
  height: 100%;
  justify-content: start;
  max-width: 400px;
  padding: 12px 0;
}
.tb_header5_content__,
.tb_header5_logo__ {
  align-items: center;
  display: flex;
  width: 100%;
}
.tb_header5_content__ {
  justify-content: center;
}
.tb_header5_logo_img__ {
  display: block;
  height: auto;
  max-height: 100%;
  max-width: 100%;
  width: auto;
}
.tb_header5_social_icons__ {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: flex-end;
  position: relative;
  width: 100%;
  z-index: 2;
}
.tb_header5_social_icon_list__ {
  align-items: center;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  margin: 0 10px;
  min-height: 30px;
  min-width: 30px;
}
.tb_header5_social_icon__.ts-facebook {
  color: #3e5c9a;
}
.tb_header5_social_icon__.ts-twitter {
  color: #29a9e1;
}
.tb_header5_social_icon__.ts-instagram {
  color: #e2306c;
}
.tb_header5_social_icon__ {
  align-items: center;
  cursor: pointer;
  display: flex;
  font-size: 24px;
  font-size: 18px;
  justify-content: center;
  text-align: center;
}
.tb_header5_subtitle__ {
  font-size: 16px;
  margin-right: 15px;
}
.tb_header5_subtitle__,
.tb_header5_title__ {
  color: #fff;
  max-width: 100%;
  overflow: hidden;
  text-align: right;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.tb_header5_title__ {
  font-size: 22px;
}
.banner_preview_ {
  transform: scale(.666);
  width: 150%;
  margin-left: -25%;
}
