.tb_header5_social_icons___ {
  position: relative;
  z-index: 2;
}
.tb_header5_social_icon__ {
  font-size: 22px;
}
@media screen and (max-width: 767px) {
  .tb_header5_column_icons__ {
    display: none;
  }
  .tb_header5_column_logo__ {
    max-width: 35%;
    padding: 0 5%;
  }
  .tb_header5_column_content__ {
    max-width: 65%;
    padding: 0 5%;
  }
  .tb_header5_content__ {
    align-items: flex-end;
    flex-direction: column;
  }
  .tb_header5_subtitle__ {
    margin-right: 0;
  }
}

