.appsMore_inner {
    background: #fff;
    box-shadow: 0 0 6px rgba(0,0,0,0.1);
}

.appsMore_wrap {
    display: flex;
}

/* LEFT SIDE CSS START */

.appsMoreleft {
    width: 300px;
    background: #F7F7FC;
    border-right: 1px solid #e4e6ef;
}

.appsMoreleft_heading, .appsMoreRight_heading {
    padding: 10px 22px;
    font-size: 13px;
    font-weight: 600;
    color: #2C313B;
    background: #F7F7FC;
    border-bottom: 1px solid #e4e6ef;
}

.appsMoreleft .nav.nav-tabs {
    /* border: 1px solid #e4e6ef; */
}

.appsMoreleft .nav.nav-tabs .nav-item {
    margin: 0;
    width: 33.33%;
}

.appsMoreleft .nav.nav-tabs .nav-item .nav-link {
    width: 100%;
    background: #fff;
    border-radius: 0;
    aspect-ratio: 1/1;
    -ms-aspect-ratio: 1/1;
    -moz-aspect-ratio: 1/1;
    -webkit-aspect-ratio: 1/1;
    padding: 8px 10px;
    display: flex;
    flex-wrap: wrap;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}

.appsMoreleft .nav.nav-tabs .nav-item .nav-link {
    border-bottom: 1px solid #e4e6ef;
}

.appsMoreleft .nav.nav-tabs .nav-item:nth-child(2) .nav-link, 
.appsMoreleft .nav.nav-tabs .nav-item:nth-child(5) .nav-link{
    border-left: 1px solid #e4e6ef;
    border-right: 1px solid #e4e6ef;
}

.appsMoreleft .nav.nav-tabs .nav-item:nth-child(8) .nav-link {
    border-left: 1px solid #e4e6ef;
    border-right: 1px solid #e4e6ef;
}

.appsMoreleft .nav.nav-tabs .nav-item:nth-child(7) .nav-link, 
.appsMoreleft .nav.nav-tabs .nav-item:nth-child(8) .nav-link {
    border-bottom: none;
}

.appsMoreleft .nav.nav-tabs .nav-item .nav-link svg {
    display: block;
    max-width: 26px;
}

.appsMoreleft .nav.nav-tabs .nav-item .nav-link span {
    display: block;
    font-size: 11px;
    font-weight: 500;
    margin-top: 5px;
}

.appsMoreleft .nav.nav-tabs .nav-item .nav-link.active {
    background: #5454Fc;
}

.appsMoreleft .nav.nav-tabs .nav-item .nav-link.active svg path {
    fill: #fff;
}

.appsMoreleft .nav.nav-tabs .nav-item .nav-link.active span {
    color: #fff;
}

.appsMoreleft .nav.nav-tabs .nav-item .nav-link.auto_moderation.active svg path+path {
    fill: none;
}

.appsMoreleft .nav.nav-tabs .nav-item .nav-link.choose_language.active svg path+path {
    fill: #9798ff;
}

/* LEFT SIDE CSS END */


/* RIGHT SIDE CSS START */

.appsMoreRight {
    width: calc(100% - 300px);
}

.appsMoreRight_heading{
    background: #fff;
    border-bottom: 1px solid #DBE1E6;
    padding-left: 15px;
}

.appsMoreRight .tab-pane .appsMoreRight_Content {
    padding: 32px;
}

.appsMoreRight .tab-content .main-tab {
    height: calc(100vh - 110px);
    overflow-y: auto;
}

.rightInner_wrap {
    padding: 15px;
    height: calc(100% - 45px);
    box-sizing: border-box;
}

.appsMoreRight .personalizePopup .nav.nav-tabs.nav-tabs-line .nav-link {
    padding-top: 0;
    font-size: 12px;
}

.mod_advance_options .card .card-header .card-title {
    padding-bottom: 20px !important;
    margin-top: 15px;
}

.mod_advance_options h6 {
    font-size: 12px;
    margin-bottom: 20px;
}

.language_inner img {
    max-width: 30px;
}

.language_inner p {
    font-size: 12px;
    margin-bottom: 0 !important;
}

.hashtagHighlighter_wrap span {
    font-size: 12px;
}

.custom_post .symbol.symbol-40 .symbol-label {
    width: 35px;
    height: 35px;
}

.upgredeMain.appsMore_empty {
    width: 100%;
    height: calc(100% - 45px);
    display: flex;
    align-items: center;
    justify-content: center;
}

.upgredeMain.appsMore_empty .ErrorMsg, .upgredeMain.download_image .ErrorMsg {
    font-size: 14px;
}

.upgredeMain.appsMore_empty .msgSec, .upgredeMain.download_image .msgSec {
    font-size: 12px;
    margin: 0 auto 20px;
    max-width: 500px;
}

.upgredeMain.appsMore_empty .Errorimage img, .upgredeMain.download_image .Errorimage img {
    max-width: 80px;
    width: 100%;
}

/* RIGHT SIDE CSS END */

.hc-95{
    height:calc(100% - 95px);
}

.hc-85{
    height:calc(100% - 85px);
}

.hc-75{
    height:calc(100% - 75px);
}

.hc-65{
    height:calc(100% - 65px);
}

.hc-55{
    height:calc(100% - 55px);
}

.hc-45{
    height:calc(100% - 45px);
}

.appsMoreRight .nav.nav-tabs .nav-item .nav-link {
    padding-top: 0;
    padding-bottom: 10px;
}

.custom_post textarea.form-control {
    height: 100px;
}

.custom_post .tab-content {
    height: calc(100% - 40px);
}

.custom_post .tab-content > .tab-pane:nth-child(2) {
    height: 100%;
}

.appsMoreRight .custom_post .bootstrap-datetimepicker-wrap {
    width: calc(100% - 48px) !important;
}

.autoModeration-tab .tab-content {
    height: calc(100% - 12px);
}

.profanityCols {
    margin: 40px 0;
}

.dropdown-toggle:after {
    border: none;
    vertical-align: middle;
}

.downloadImage_select_wall{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 100vh;
    justify-content: space-between;
    height: calc(100% - 46px);
}

.collaborator_inner {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 100vh;
    justify-content: space-between;
    height: calc(100% - 90px);
}

.api_inner .modal-content {
    height: 100%;
    box-shadow: none;
}

.api_inner .color-demo {
    display: flex;
}

.api_inner .color-demo a{
    background: #FFFFFF;
    border: 1px solid #DEDEDE;
    border-radius: 6px;
    margin-top: 15px;
    transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    -webkit-transition: all 0.3s;
}

.api_inner .color-demo a:first-child{
    margin-right: 25px;
}

.api_inner .color-demo .color-view{
    margin: 0;
    padding: 25px 0;
}

.api_inner .color-demo a:hover, .api_inner .color-demo a.active{
    border-color: #4E69ED;
}



/* RESPONSIVE CSS START */

@media only screen and (max-width: 1520px) and (min-width: 1367px){

    .appsMoreleft .nav.nav-tabs .nav-item .nav-link {
        min-height: 100px;
    }

    .appsMoreleft .nav.nav-tabs .nav-item .nav-link span {
        margin-top: 10px;
    }

    

}

@media only screen and (max-width: 1366px) and (min-width: 992px){

    .appsMoreleft {
        width: 28%;
    }

    .appsMoreRight {
        width: 72%;
    }

    .appsMoreleft .nav.nav-tabs .nav-item {
        width: 50%;
    }

    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(4) .nav-link,
    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(6) .nav-link {
        border-left: 1px solid #e4e6ef;
    }

    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(2) .nav-link, 
    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(5) .nav-link,
    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(8) .nav-link {
        border-right: none;
    }

    .color-demo .color-view{
        width:275px;
    }

    .pay_details.dashPayModal {
        height: auto;
    }

}


@media only screen and (max-width: 1199px) and (min-width: 992px){

    .appsMoreleft .nav.nav-tabs .nav-item .nav-link {
        min-height: 96px;
    }

    .appsMoreleft .nav.nav-tabs .nav-item .nav-link svg {
        max-width: 25px;
    }
    .appsMoreleft .nav.nav-tabs .nav-item .nav-link span {
        margin-top: 0px;
    }

}


@media screen and (max-width:991px){

    .appsMoreRight .tab-content .main-tab {
        height: auto;
    }

    .appsMore_wrap {
        display: block;
    }

    .appsMoreleft, .appsMoreRight {
        width: 100%;
    }

    .appsMoreleft .nav.nav-tabs .nav-item {
        width: 25%;
    }

    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(4) .nav-link{
        border-left: 1px solid #e4e6ef;
    }

    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(6) .nav-link {
        border-right: 1px solid #e4e6ef;
    }

    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(5) .nav-link, 
    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(6) .nav-link {
        border-bottom: none;
    }

    .appsMoreRight_heading {
        background: #f7f7fc;
    }

}


@media screen and (max-width:767px){

    .rightInner_wrap {
        padding: 20px 15px;
    }

    .api_inner.rightInner_wrap .color-demo a {
        width: 50%;
        padding: 0 15px;
        margin: 0 7.5px;
    }

    .api_inner.rightInner_wrap .color-demo .color-view {
        padding: 25px 15px;
        width: 100%;
    }

    .appsMoreleft_heading, .appsMoreRight_heading {
        padding: 10px 15px;
    }

    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(5) .nav-link {
        border-right: none;
    }

    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(6) .nav-link {
        border-left: 1px solid #e4e6ef;
    }

}

@media screen and (max-width:575px){
    .appsMoreleft .nav.nav-tabs .nav-item .nav-link svg {
        max-width: 25px;
    }

    .appsMoreleft .nav.nav-tabs .nav-item .nav-link span {
        margin-top: 5px;
    }

    .appsMoreleft .nav.nav-tabs .nav-item .nav-link {
        min-height: 50px;
    }

    .rightInner_wrap .custom-post_upload-image .fileinput-preview, .rightInner_wrap .fileinput .uppy-Dashboard-inner {
        height: 180px !important;
    }

    .upgredeMain.appsMore_empty .Errorimage img, .upgredeMain.download_image .Errorimage img {
        max-width: 60px;
        width: 100%;
    }
}

@media screen and (max-width:420px){

    .appsMoreleft .nav.nav-tabs .nav-item {
        width: 33.33%;
    }

    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(4) .nav-link,
    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(6) .nav-link {
        border-left: none;
    }

    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(4) .nav-link{
        border-bottom: none;
    }

    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(5) .nav-link{
        border-bottom: none;
        border-right: 1px solid #e4e6ef;
    }

    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(7) .nav-link,
    .appsMoreleft .nav.nav-tabs .nav-item:nth-child(8) .nav-link{
        border-top: 1px solid #e4e6ef;
    }

    .api_inner.rightInner_wrap .color-demo .color-view img {
        max-width: 60px;
    }

    .appsMoreRight .custom-post_upload-image .fileinput-preview, 
    .appsMoreRight .fileinput .uppy-Dashboard-inner {
        height: 170px !important;
    }

    .appsMoreRight .productItem.addMore button, 
    .appsMoreRight .addProUploder .click_text{
        margin-bottom: 22px;
    }
    
}

@media screen and (max-width:360px){
    .card-toolbar .btn {
        margin-bottom: 20px;
    }

    .appsMoreRight .Errorimage img {
        max-width: 150px;
    }

    .rightInner_wrap .wallcreateshow .modal-body {
        padding: 0;
    }

    .api_inner.rightInner_wrap .color-demo .color-view img {
        max-width: 40px;
    }
}